import React, { Component } from 'react';

import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip  } from 'recharts';

import { makeStyles } from '@material-ui/core/styles';

import { lodashToggle } from '../utils';


export function genData (data, param) {
  let dataParams = []
  console.log(data)
  data.map(record => {
    if (record.record && record.record[param] )
      dataParams = lodashToggle(dataParams, {ts: tsToDateTime(record.ts), [param]: record.record[param]})
  })
  return dataParams
}

function getModalStyle() {
  const top = 50 
  const left = 50

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    // width: 1000,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

function ChartBody(props) {
  const classes = useStyles();
  const [modalStyle] = React.useState(getModalStyle);

  return (
    <div style={modalStyle} className={classes.paper}>
      <h4 className='mr-5'>{props.label}</h4>
      <RenderLineChart data={props.data} param={props.param}/>
    </div>
  );
}

export class ChartModal extends Component {
  constructor(props) {
   super(props);

   this.state = {
    //  eng: false,
   }
 }

 componentDidMount() {
    let today = new Date();
    let yesterday = today.setDate(today.getDate() - 1);
    let query = {ws: this.props.query.ws, sect: '', t1: yesterday, t2: today, param: this.props.query.param}
    if (this.props.query.sect)
      query['sect'] = this.props.query.sect
    this.props.getESystemsDataDetail(query)
 }

 render () {
   const { detailDataList } = this.props
   const data = genData(detailDataList, this.props.query.param)

   return (
    <ChartBody data={data} label={this.props.label} param={this.props.query.param}/>
   )
 }
}


export function RenderLineChart (props) {
  const { data, param } = props
  return (
    <LineChart width={700} height={200} data={data}>
      <Line type="monotone" dataKey={param} stroke="#BF263C" />
      <CartesianGrid stroke="#ccc" />
      <XAxis dataKey="ts" />
      <YAxis />
      <Tooltip />
    </LineChart>
  )
}

 export function tsToDateTime(ts) {
    let date = new Date(ts);
    let dd = String(date.getDate()).padStart(2, '0');
    let mm = String(date.getMonth() + 1).padStart(2, '0');
    let yyyy = date.getFullYear();
    return `${dd}/${mm}T${date.getHours()}:${date.getMinutes()}`
}